.block-panel {
  padding: 16px;
  background: #fff;
  border-right: 1px solid #ddd;
  overflow-y: auto;
}
.block-panel h3 {
  margin-top: 12px;
  margin-bottom: 6px;
}
.block-panel button {
  display: block;
  width: 100%;
  margin: 4px 0;
  padding: 8px;
  font-size: 14px;
  background: #4a90e2;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.block-panel button:hover {
  background: #357ab8;
}
